import gql from 'graphql-tag';
export const SUB_AUTHENTICATED = gql `
  subscription($id: String!) {
    Users(where: { id: { _eq: $id } }, limit: 1) {
      id
      name
      password
      email
      role
    }
  }
`;
export const SUB_USER = gql `
  subscription {
    Users {
      id
      name
      email
      role
      password
      Posts {
        id
        title
        body
        created
        date
      }
    }
  }
`;
export const SUB_POST = gql `
  subscription {
    Posts {
      id
      title
      date
      created
      User {
        id
        name
        email
        role
      }
      Comments_aggregate {
        aggregate {
          count
        }
      }
    }
  }
`;
export const SUB_COMMENT = gql `
  subscription($id: String!) {
    Comments(where: { postId: { _eq: $id } }, order_by: { created: desc }) {
      id
      body
      created
      name
    }
  }
`;
