import gql from 'graphql-tag';
const GET_USERS = gql `
  {
    Users {
      id
      name
      email
      role
      Posts {
        id
        title
        body
      }
    }
  }
`;
const GET_POSTS = gql `
  {
    Posts {
      id
      title
      body
      User {
        id
        name
      }
      Comments {
        id
        body
        name
      }
    }
  }
`;
const GET_USER_EMAIL = gql `
  query($email: String) {
    Users(where: { email: { _eq: $email } }) {
      id
      name
      email
      role
      Posts {
        id
        title
      }
    }
  }
`;
const GET_USER_ID = gql `
  query($id: String!) {
    Users(limit: 1, where: { id: { _eq: $id } }) {
      id
      name
      email
      password
      role
    }
  }
`;
const GET_POST_ID = gql `
  query($id: String!) {
    Posts(limit: 1, where: { id: { _eq: $id } }) {
      id
      title
      body
      date
      created
      User {
        id
        name
      }
      Comments {
        id
        body
        name
      }
    }
  }
`;
export { GET_USERS, GET_POSTS, GET_USER_ID, GET_USER_EMAIL, GET_POST_ID };
