<script lang="ts" context="module">
  import type { Post, User /*App*/ } from 'app-interfaces';
  import { USER_ROLES } from '../../bin/models/StaticTypes';
  // import { getContext } from 'svelte';
  import {
    Button,
    Modal,
    ModalBody,
    ModalHeader,
    ModalFooter,
    FormGroup,
    Label,
  } from 'sveltestrap';
</script>

<script lang="ts">
  export let props: {
    id?: string;
    data: User;
    edit: boolean;
    loading: boolean;
    modalProps: {
      transitionOptions: {};
      size: string;
      centered: boolean;
      backdrop: 'static';
      isOpen: boolean;
      toggle(): void;
    };
    actions: {
      [x: string]: (data: Post | User, callback?: () => void) => void;
    };
  };
  let { data, edit, modalProps, actions } = props;

  const handleSubmit = (e) => {
    const formData = new FormData(e.currentTarget);
    const params = {};
    formData.forEach((v, k) => Object.assign(params, { [k]: v }));
    console.log(params);
  };
</script>

<Modal {...modalProps}>
  <ModalHeader toggle={modalProps.toggle} class="bg-dark text-light">
    User Details
  </ModalHeader>
  <ModalBody>
    {#if edit}
      <form id="user-form" on:submit|preventDefault={handleSubmit}>
        <FormGroup>
          <Label>Name</Label>
          <input
            type="text"
            class="form-control"
            bind:value={data.name}
            placeholder="Full Name" />
        </FormGroup>
        <FormGroup>
          <Label>Email</Label>
          <input
            type="email"
            class="form-control"
            bind:value={data.email}
            placeholder="Email address" />
        </FormGroup>
        <FormGroup>
          <Label>Role</Label>
          <select class="form-control" bind:value={data.role}>
            <option value={USER_ROLES.user}>User</option>
            <option value={USER_ROLES.admin}>Admin</option>
          </select>
        </FormGroup>
      </form>
    {:else}
      <section>{data.name}</section>
      <section>{data.email}</section>
      <section>{data.role}</section>
    {/if}
  </ModalBody>
  <ModalFooter>
    <Button
      outline={edit}
      color={!edit ? 'warning' : 'primary'}
      on:click={!edit ? () => (edit = !edit) : () => actions['update'](data)}>
      {!edit ? 'Edit' : 'Done'}
    </Button>
    <Button
      outline
      color="secondary"
      on:click={!edit ? modalProps.toggle : () => (edit = !edit)}>
      Cancel
    </Button>
  </ModalFooter>
</Modal>
