<script lang="ts">
  import type { App, User } from 'app-interfaces';
  import { getContext, afterUpdate } from 'svelte';
  import { fade } from 'svelte/transition';
  import { FormGroup, Label, Button } from 'sveltestrap';
  const {
    state: { view, authenticated },
    Methods: { getUserEmail, login, register, addUser },
  } = getContext('state') as App;
  let error: string;
  const handleLogin = async (e) => {
    const formData = new FormData(e.currentTarget);
    switch ($view) {
      case 'login':
        try {
          const params = { email: '', password: '' };
          formData.forEach((v, k) => Object.assign(params, { [k]: v }));
          const data: {
            state: string;
            user: User;
            message?: string;
          } = await login(params);
          if (data.state === 'success') {
            const user = await getUserEmail(data.user.email);
            if (user) return authenticated.set(user), view.set('');
            else if (!user) {
              const registerUser = await addUser({
                email: data.user.email,
                name: data.user.name,
                password: data.user.password,
                role: data.user.role,
              });
              if (registerUser)
                return authenticated.set(registerUser), view.set('');
            }
          } else if (data.state === 'failure') {
            console.warn(data.message);
            error = data.message;
          }
        } catch (error) {
          console.warn(error);
        }
        break;
      case 'register':
        try {
          const params = { email: '', password: '', name: '' };
          formData.forEach((v, k) => Object.assign(params, { [k]: v }));
          const data: {
            state: string;
            user: User;
            message?: string;
          } = await register(params);
          if (data.state === 'success') {
            const user = await getUserEmail(data.user.email);
            if (user)
              return authenticated.update(() => user), view.update(() => '');
            else if (!user) {
              const registerUser = await addUser({
                email: data.user.email,
                name: data.user.name,
                password: data.user.password,
                role: data.user.role,
              });
              if (registerUser)
                return (
                  authenticated.update(() => registerUser),
                  view.update(() => '')
                );
            }
          } else if (data.state === 'failure') {
            console.warn(data.message);
            error = data.message;
          }
        } catch (error) {
          console.warn(error);
        }
        break;
      default:
        break;
    }
  };
  afterUpdate(() => {
    if (error) {
      setTimeout(() => (error = null), 2000);
    }
    if ($authenticated) {
      setTimeout(() => view.update((n) => ''), 800);
    }
  });
</script>

<style>
  div.container {
    max-width: 450px;
    border-radius: 50px;
  }
</style>

<div class="container">
  <form id={`user-${$view}`} on:submit|preventDefault={handleLogin}>
    {#if $view === 'login'}
      <FormGroup>
        <Label>Email</Label>
        <input
          class="form-control"
          type="email"
          name="username"
          placeholder="Enter email address" />
      </FormGroup>
      <FormGroup>
        <Label>Password</Label>
        <input
          class="form-control"
          type="password"
          name="password"
          placeholder="Enter password" />
      </FormGroup>
    {:else if $view === 'register'}
      <FormGroup>
        <Label>Name</Label>
        <input
          class="form-control"
          type="name"
          name="name"
          placeholder="Enter full name" />
      </FormGroup>
      <FormGroup>
        <Label>Email</Label>
        <input
          class="form-control"
          type="email"
          name="username"
          placeholder="Enter email address" />
      </FormGroup>
      <FormGroup>
        <Label>Password</Label>
        <input
          class="form-control"
          type="password"
          name="password"
          placeholder="Enter password" />
      </FormGroup>
    {/if}
    {#if error}
      <div
        class="container bg-warning text-dark"
        transition:fade={{ duration: 300, delay: 300 }}>
        {error}
      </div>
    {/if}
    <hr />
    <Button
      type="submit"
      color="primary"
      outline
      class="m-auto"
      style="text-transform:capitalize;">
      {$view}
    </Button>
  </form>
</div>
{#if $view === 'login'}
  <section>
    <p>
      Not registered? <i style="cursor:pointer;" on:click={() => view.update((n) => 'register')}>click
        to register</i>
    </p>
  </section>
{:else if $view === 'register'}
  <section>
    <p>
      Already registered? <i style="cursor:pointer;" on:click={() => view.update((n) => 'login')}>click
        to login</i>
    </p>
  </section>
{/if}
