<script lang="ts" context="module">
  import { getContext, onMount } from 'svelte';
  import { writable } from 'svelte/store';
  import { fly } from 'svelte/transition';
  import { formatDistanceToNow } from 'date-fns';
  import { CardTitle, CardBody, CardText, Badge } from 'sveltestrap';
  import type { App, Comment, Post } from 'app-interfaces';
  import { USER_ROLES } from '../bin/models/StaticTypes';
  import Details from '../Components/Details.svelte';
  import Add from '../Components/Add.svelte';
  import { subscribe } from 'svelte-apollo';
  import client from '../bin/config/GraphQL';
  import { SUB_POST } from '../bin/models/Subscriptions';
</script>

<script lang="ts">
  const {
    state: { posts, authenticated },
    Methods: { addPost, updatePostId, addComment },
  } = getContext('state') as App;
  const id = writable<string | null>(null);
  const open = writable(false);
  const add = writable(false);
  const subPosts = subscribe(client, { query: SUB_POST });

  const actions = {
    async add(data: Partial<Post>) {
      const params = {
        title: data.title,
        body: data.body,
        userId: $authenticated.id,
      };
      const newPosts = await addPost(params);
      if (newPosts) posts.update((prevPosts) => [...prevPosts, newPosts]);
      if ($add) add.update(() => false);
    },
  };
  const handleDispatch = async (e: CustomEvent) => {
    const { data, callback } = e.detail;
    switch (e.type) {
      case 'add-post':
        try {
          const params = {
            title: data.title,
            body: data.body,
            userId: $authenticated.id,
            date: new Date(data.date),
          };
          const newPost = await addPost(params);
          if (newPost) posts.update((prevPosts) => [...prevPosts, newPost]);
          if ($add) add.update(() => false);
        } catch (error) {
          console.warn(error);
        }
        break;
      case 'update-post':
        try {
          const params = {
            id: data.id,
            title: data.title,
            body: data.body,
            userId: data.User.id,
            date: new Date(data.date),
          };
          await updatePostId(params);
          callback();
        } catch (error) {
          console.warn(error);
        }
        break;
      case 'add-comment':
        try {
          const params: Partial<Comment> = {
            body: data.body,
            name: data.name,
            email: data.email,
            postId: data.postId,
          };
          if ($authenticated) {
            params.name = $authenticated.name;
            params.email = $authenticated.email;
          }
          await addComment(params);
          callback();
        } catch (error) {
          console.warn(error);
        }
        break;
      default:
        break;
    }
  };

  const handleClick = (e: any) => id.update(() => e.currentTarget.id);
  const handleClose = () => id.update(() => undefined);
  $: {
    $id ? open.update(() => true) : open.update(() => false);
  }
</script>

<style>
  .section-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 1rem;
    column-gap: 0.5rem;
  }
  .card {
    margin-bottom: 1rem;
    color: #3f3f3f;
    cursor: pointer;
  }
  .card.primary {
    color: var(--success);
    border: 1px solid var(--success);
    border-radius: 5px;
  }
  .card:hover {
    background-color: #3f3f3f;
    color: #ffffff;
  }
  .card h2 {
    padding: 0 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-transform: capitalize;
  }
  @media (max-width: 640px) {
    .section-grid {
      grid-template-columns: 1fr;
    }
  }
</style>

<div class="container" in:fly={{ duration: 300, x: 300 }}>
  {#if $add && $authenticated && $authenticated.role === USER_ROLES.admin}
    <Add
      on:add-post={handleDispatch}
      props={{ actions, toggle: () => add.update(() => false) }} />
  {/if}
  {#if $authenticated && $authenticated.role === USER_ROLES.admin}
    <div
      class="card primary"
      on:click={() => add.update((prev) => !prev)}
      disabled={$add}>
      {#if $add}Cancel{:else}Add new Post{/if}
    </div>
  {/if}
  {#await $subPosts}
    <div class="card">Loading...</div>
  {:then result}
    <section class="section-grid">
      {#each result.data.Posts as post}
        <div
          class="card"
          id={post['id']}
          on:click={handleClick}
          in:fly={{ duration: 300, x: 300 }}>
          <CardTitle class="bg-secondary text-light">
            <h2>{post['title']}</h2>
          </CardTitle>
          <CardBody>
            <CardText>
              <Badge>{post['Comments_aggregate']['aggregate']['count']}</Badge>
              Wish{post['Comments_aggregate']['aggregate']['count'] > 1 ? 'es' : ''}
              collected
            </CardText>
            <CardText>
              {formatDistanceToNow(new Date(post['date']), { addSuffix: true })}
            </CardText>
            <Badge>- {post['User']['name'].split(' ')[0]} -</Badge>
          </CardBody>
        </div>
      {:else}
        <section style="max-width:450px;margin:0 auto;">
          <div class="container">No Posts added</div>
        </section>
      {/each}
    </section>
  {:catch error}
    <div class="card bg-warning text-dark">Error loading Posts: {error}</div>
  {/await}

  {#if $id}
    <Details
      on:add-comment={handleDispatch}
      on:update-post={handleDispatch}
      props={{ id: $id, open: $open, toggle: handleClose, actions }} />
  {/if}
</div>
