<script lang="ts" context="module">
  import type { Comment, Post, App } from 'app-interfaces';
  import type { MODAL_PROPS } from '../../bin/models/StaticTypes';

  import { getContext, createEventDispatcher } from 'svelte';
  import { writable } from 'svelte/store';
  import { fly } from 'svelte/transition';
  import {
    Button,
    Modal,
    ModalBody,
    ModalHeader,
    ModalFooter,
    ListGroup,
    ListGroupItem,
    Input,
    Spinner,
    FormGroup,
    Label,
    Badge,
  } from 'sveltestrap';
  import { subscribe } from 'svelte-apollo';
  import { format, formatDistanceToNow } from 'date-fns';

  import { USER_ROLES } from '../../bin/models/StaticTypes';
  import client from '../../bin/config/GraphQL';
  import { SUB_COMMENT } from '../../bin/models/Subscriptions';
</script>

<script lang="ts">
  export let props: {
    data: Post;
    edit: boolean;
    modalProps: MODAL_PROPS;
  };

  const dispatch = createEventDispatcher();

  const {
    state: { authenticated, subComments },
  } = getContext('state') as App;

  const newComment: Partial<Comment> = {
    body: null,
    name: null,
    email: null,
    postId: props.data.id,
  };

  const addComment = writable(false);
  const Comments = subComments(props.data.id);
  const toggleAddComment = () => {
    newComment.body = null;
    newComment.name = null;
    newComment.email = null;
    return addComment.update((n) => !n);
  };
  const toggleEdit = () => (props.edit = !props.edit);

  const handleAddComment = () =>
    dispatch('add-comment', {
      data: newComment,
      callback: toggleAddComment,
    });
  const handleUpdatePost = () =>
    dispatch('update-post', {
      data: props.data,
      callback: toggleEdit,
    });
</script>

<style>
  div.d-grid-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  section.comments {
    max-height: 160px;
    overflow: auto;
  }
</style>

<Modal {...props.modalProps}>
  <ModalHeader toggle={props.modalProps.toggle} class="bg-warning text-dark">
    {props.edit ? 'Edit ' : ''}Post Details
  </ModalHeader>
  <ModalBody>
    {#if props.data && 'title' in props.data}
      <section class="title">
        {#if props.edit}
          <FormGroup>
            <Label>Title</Label>
            <Input
              type="text"
              readonly={false}
              size="12"
              bind:value={props.data['title']} />
          </FormGroup>
        {:else}
          <h2>{props.data.title}</h2>
        {/if}
      </section>
      <section class="body">
        {#if props.edit}
          <FormGroup>
            <Label>Body</Label>
            <textarea
              rows={3}
              class="form-control"
              bind:value={props.data['body']} />
          </FormGroup>
          <FormGroup>
            <Label>Date</Label>
            <input
              type="date"
              class="form-control"
              bind:value={props.data['date']} />
          </FormGroup>
        {:else}
          {#if 'body' in props.data}
            <p>
              {@html props.data.body}
            </p>
            <p>{format(new Date(props.data.date), 'eee dd MMM yyyy')}</p>
          {/if}
          {#if 'User' in props.data}
            <Badge>{props.data.User.name.split(' ')[0]}</Badge>
          {/if}
        {/if}
      </section>
      {#if !props.edit}
        <hr />
        <h5 on:click={toggleAddComment}>Comments</h5>
        {#if $addComment}
          <section transition:fly={{ duration: 300, y: -30 }}>
            <ListGroup>
              <ListGroupItem>
                <form on:submit|preventDefault={handleAddComment}>
                  <FormGroup>
                    <textarea
                      required
                      rows={2}
                      class="form-control"
                      placeholder="Add a new comment..."
                      bind:value={newComment['body']} />
                  </FormGroup>
                  {#if !$authenticated}
                    <div class="d-grid-2">
                      <FormGroup>
                        <Label>Name:</Label>
                        <input
                          required
                          type="text"
                          class="form-control"
                          placeholder="Enter your name"
                          bind:value={newComment['name']} />
                      </FormGroup>
                      <FormGroup>
                        <Label>Email:</Label>
                        <input
                          required
                          type="email"
                          class="form-control"
                          placeholder="Enter your email"
                          bind:value={newComment['email']} />
                      </FormGroup>
                    </div>
                  {/if}
                  <hr />
                  <button type="submit" class="btn btn-outline-primary">
                    Add Comment
                  </button>
                </form>
              </ListGroupItem>
              <hr />
            </ListGroup>
          </section>
        {/if}
        <section class="comments">
          {#await $Comments}
            <div class="card">loading...</div>
          {:then result}
            <ListGroup>
              {#each result.data.Comments as comment (comment['id'])}
                <div class="card" in:fly={{ duration: 300, y: -30 }}>
                  <ListGroupItem>
                    <p>
                      {@html comment['body']}
                    </p>
                    <Badge>
                      {comment['name'].split(' ', 1)}
                      {comment['name'].split(' ')[1].split('', 1)}
                    </Badge>
                  </ListGroupItem>
                </div>
              {:else}
                <div class="card" in:fly={{ duration: 300, y: -30 }}>
                  <ListGroupItem>
                    <p>No Comments added</p>
                  </ListGroupItem>
                </div>
              {/each}
            </ListGroup>
          {:catch error}
            <div class="card bg-warning text-dark">
              Error loading comments: {JSON.stringify(error)}
            </div>
          {/await}
        </section>
      {/if}
    {:else}
      Loading ...
      <Spinner color={'primary'} />
    {/if}
  </ModalBody>
  {#if $authenticated && ($authenticated.email === props.data.User.email || $authenticated.role === USER_ROLES.admin)}
    <ModalFooter>
      <Button
        outline={props.edit}
        color={!props.edit ? 'warning' : 'primary'}
        on:click={!props.edit ? toggleEdit : handleUpdatePost}>
        {!props.edit ? 'Edit' : 'Done'}
      </Button>
      <Button
        outline
        color="secondary"
        on:click={!props.edit ? props.modalProps.toggle : toggleEdit}>
        Close
      </Button>
    </ModalFooter>
  {/if}
</Modal>
