<script lang="ts">
  import type { App, Comment, Post, User } from 'app-interfaces';
  import { getContext, onMount } from 'svelte';
  import { fly } from 'svelte/transition';

  import PostModal from './Modals/PostDetails.svelte';
  import UserModal from './Modals/UserDetails.svelte';
  export let props: {
    id?: string;
    open: boolean;
    toggle: () => void;
    actions: {
      [x: string]: (data: Post | User | Comment, callback?: () => void) => void;
    };
  };
  const {
    state: { view },
    Methods: { getUserId, getPostId },
  } = getContext('state') as App;
  let data: Post | User;
  let loading: boolean;
  let edit: boolean;

  const modalProps = {
    transitionOptions: { fade: { duration: 300, delay: 300 } },
    size: 'lg',
    centered: true,
    backdrop: 'static' as 'static',
    isOpen: props.open,
    toggle: props.toggle,
  };
  onMount(async () => {
    if (!props.id) return;
    switch ($view) {
      case 'posts':
        try {
          if (!loading) loading = true;
          data = await getPostId(props.id);
        } catch (error) {
          console.warn(error);
        }
        loading = false;
        break;
      case 'users':
        try {
          if (!loading) loading = true;
          data = await getUserId(props.id);
        } catch (error) {
          console.warn(error);
        }
        loading = false;
        break;
      default:
        break;
    }
  });
</script>

<div class="container" in:fly={{ duration: 300, x: 30 }}>
  {#if $view === 'posts' && data && 'title' in data}
    <PostModal
      on:add-comment
      on:update-post
      props={{ modalProps, data, edit }} />
  {:else if $view === 'users' && data && 'name' in data}
    <UserModal
      props={{ modalProps, actions: props.actions, data, edit, id: props.id, loading }} />
  {/if}
</div>
