<script lang="ts">
  import { setContext, onMount } from 'svelte';
  import { AppContext } from './bin/config/Store';
  import Navbar from './Components/Navbar.svelte';
  import Home from './Pages/Home.svelte';
  import Login from './Pages/Login.svelte';
  import Users from './Pages/Users.svelte';
  import Posts from './Pages/Posts.svelte';
  import Profile from './Pages/Profile.svelte';

  setContext('state', AppContext);

  const {
    state: { view, authenticated },
    Methods: { loggedin, getUserEmail, addUser },
  } = AppContext;

  $: title = $view;

  const handleView = (e: any) => {
    view.set(e.currentTarget.id);
    localStorage.setItem('view', $view);
  };
  onMount(() => {
    if (!$authenticated) {
      loggedin(async (data) => {
        if (data.state === 'success') {
          const user = await getUserEmail(data.user.email);
          if (user) return authenticated.set(user);
          const registerUser = await addUser({
            name: data.user.name,
            email: data.user.email,
            password: data.user.password,
          });
          if (registerUser) return authenticated.set(registerUser);
        }
      });
    }
    const lsState = localStorage.getItem('view');
    if (!lsState) return localStorage.setItem('view', $view);
    else return view.set(lsState);
  });
</script>

<style>
  @import url('https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css');
  :global(main) {
    text-align: center;
    padding: 1em;
    max-width: 450px;
    margin: 0 auto;
  }
  :global(ul) {
    margin-bottom: 0;
  }
  :global(li) {
    list-style: none;
  }
  :global(textarea) {
    resize: none;
  }
  :global(.card) {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    min-width: 0;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0, 0, 0, 0.125);
    border-radius: 0.25rem;
  }
  :global(.container) {
    width: 100%;
    margin: auto;
  }

  @media (min-width: 640px) {
    main {
      max-width: none;
    }
  }
</style>

<svelte:head>
  <title>Wish Collector{title ? ` | ${title}`.toUpperCase() : ''}</title>
</svelte:head>
<Navbar {handleView} />
<main>
  {#if $view === ''}
    <Home {handleView} />
  {:else if $view === 'login' || $view === 'register'}
    <Login />
  {:else if $view === 'posts'}
    <Posts />
  {:else if $view === 'users'}
    <Users />
  {:else if $view === 'profile'}
    <Profile />
  {/if}
</main>
