import { writable } from 'svelte/store';
import { query, mutate, subscribe } from 'svelte-apollo';
import client from './GraphQL';
import { GET_POSTS, GET_POST_ID, GET_USERS, GET_USER_ID, GET_USER_EMAIL, } from '../models/Queries';
import { ADD_POST, UPDATE_POST, ADD_USER, UPDATE_USER, ADD_COMMENT, UPDATE_COMMENT, } from '../models/Mutations';
import { SUB_COMMENT, SUB_POST, SUB_USER, SUB_AUTHENTICATED, } from '../models/Subscriptions';
const baseUrl = `/api`;
const authUrl = `${baseUrl}/auth`;
class Context {
    constructor() {
        this.state = {
            view: writable(''),
            users: writable([]),
            posts: writable([]),
            authenticated: writable(undefined),
            subUsers: subscribe(client, { query: SUB_USER }),
            subPosts: subscribe(client, { query: SUB_POST }),
            subAuth: (id) => subscribe(client, { query: SUB_AUTHENTICATED, variables: { id } }),
            subComments: (postId) => subscribe(client, { query: SUB_COMMENT, variables: { id: postId } }),
        };
        this.Methods = {
            updateView: this.updateView.bind(this),
            login: this.login.bind(this),
            register: this.register.bind(this),
            loggedin: this.loggedin.bind(this),
            logout: this.logout.bind(this),
            getUsers: this.getUsers.bind(this),
            getPosts: this.getPosts.bind(this),
            getUserEmail: this.getUserEmail.bind(this),
            getUserId: this.getUserId.bind(this),
            getPostId: this.getPostId.bind(this),
            addUser: this.addUser.bind(this),
            addPost: this.addPost.bind(this),
            addComment: this.addComment.bind(this),
            updateUserId: this.updateUserId.bind(this),
            updatePostId: this.updatePostId.bind(this),
            updateCommentId: this.updateCommentId.bind(this),
        };
    }
    /* View Handler */
    updateView(name) {
        this.state.view.update(() => name);
    }
    /* Register User */
    async register(params) {
        const response = await fetch(`${authUrl}/register`, {
            method: 'POST',
            body: JSON.stringify(params),
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await response.json();
        return data;
    }
    /* Login User */
    async login(params) {
        try {
            const response = await fetch(`${authUrl}/login`, {
                method: 'POST',
                body: JSON.stringify(params),
                headers: {
                    'Content-Type': 'application/json',
                },
            });
            const data = await response.json();
            return data;
        }
        catch (error) {
            console.warn(error);
        }
    }
    /* Check if Logged In */
    async loggedin(callback) {
        const response = await fetch(`${authUrl}/isloggedin`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
        });
        const data = await response.json();
        return callback(data);
    }
    /* Logout User */
    async logout(callback) {
        await fetch(`${authUrl}/logout`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
        });
        return callback();
    }
    /* Get Collection */
    async getUsers() {
        const { data: { Users }, } = await query(client, { query: GET_USERS }).result();
        if (Users)
            this.state.users.update((prevUsers) => Users);
    }
    async getPosts() {
        const { data: { Posts }, } = await query(client, { query: GET_POSTS }).result();
        if (Posts)
            this.state.posts.update((prevPosts) => Posts);
    }
    /* Get Document */
    async getUserEmail(email) {
        const { data: { Users }, } = await query(client, {
            query: GET_USER_EMAIL,
            variables: { email },
        }).result();
        if (Users)
            return Users[0];
    }
    async getUserId(id) {
        const { data: { Users }, } = await query(client, {
            query: GET_USER_ID,
            variables: { id },
        }).result();
        if (Users)
            return Users[0];
    }
    async getPostId(id) {
        const { data: { Posts }, } = await query(client, {
            query: GET_POST_ID,
            variables: { id },
        }).result();
        if (Posts)
            return Posts[0];
    }
    /* Add Document */
    async addUser({ name, email, password, role }) {
        const { data: { insert_Users_one }, } = await mutate(client, {
            mutation: ADD_USER,
            variables: { name, email, password, role },
        });
        if (insert_Users_one)
            return insert_Users_one;
    }
    async addPost({ title, body, userId, date }) {
        const { data: { insert_Posts_one }, } = await mutate(client, {
            mutation: ADD_POST,
            variables: { title, body, userId, date: date.toISOString() },
        });
        if (insert_Posts_one)
            return insert_Posts_one;
    }
    async addComment({ body, name, email, postId }) {
        const { data: { insert_Comments_one }, } = await mutate(client, {
            mutation: ADD_COMMENT,
            variables: { body, name, email, postId },
        });
        if (insert_Comments_one)
            return insert_Comments_one;
    }
    /* Update Document */
    async updateUserId({ id, name, email, role, password }) {
        const { data: { update_Users }, } = await mutate(client, {
            mutation: UPDATE_USER,
            variables: { id, name, email, password, role },
        });
        if (update_Users)
            return update_Users.returning[0];
    }
    async updatePostId({ id, title, body, userId, date }) {
        const { data: { update_Posts }, } = await mutate(client, {
            mutation: UPDATE_POST,
            variables: { id, title, body, userId, date: date.toISOString() },
        });
        if (update_Posts)
            return update_Posts.returning[0];
    }
    async updateCommentId({ id, body }) {
        const { data: { update_Comments }, } = await mutate(client, {
            mutation: UPDATE_COMMENT,
            variables: { id, body },
        });
        if (update_Comments)
            return update_Comments.returning[0];
    }
}
export const AppContext = new Context();
